import { INSTALLED_KEY } from "./utilities/constants";

//TODO: Version also needs to be returned from here.

export const makeInstaller = (components = []) => {
  const install = (app) => {
    if (app[INSTALLED_KEY]) return;

    app[INSTALLED_KEY] = true;
    components.forEach((c) => app.use(c));
  };

  return {
    install
  };
};
